.Modal {
  /*width: calc(100vw - 40px);*/
  margin: 0 auto;
  position: fixed;
  /*position: relative;*/
  left: 0;
  right: 0;
  top: 0;
  /*background: white;*/
  /*padding: 20px;*/
  /*height: calc(100vh - 40px);*/
  height: 100vh;
  width: 100vw;
  opacity: 0;
  z-index: 4;
  -webkit-backdrop-filter: blur(0px);
          backdrop-filter: blur(0px);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

#close-button-with-OtherModalLayout.inactive {
  overflow-y: hidden;
}

.ConfirmationModal {
  background: black;
  background: -webkit-gradient(linear, left top, right bottom, from(#00000099), to(#000000f0));
  background: linear-gradient(to bottom right, #00000099, #000000f0);
}

.local_loader {
  position: absolute;
  align-self: center;
  justify-self: center;
}

.local_loader_container {
  width: 100%;
  height: 100%;
  background: #ffffffc2;
  position: absolute;
  display: grid;
}

.show_loader_app {
  opacity: 1;
}

.Modal.aparecer {
  -webkit-animation-name: aparece;
          animation-name: aparece;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

@-webkit-keyframes aparece {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes aparece {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Modal-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -25px;
  top: -25px;
  border-radius: 50%;
  border: none;
  background: #171c30;
  color: white;
  cursor: pointer;
}

.Modal-close:after {
  content: "X";
  font-weight: bold;
  font-size: 30px;
}

.modalView {
  background: white;
  opacity: 0.95;
}

.modalSuccess {
  background: -webkit-gradient(linear, left top, right bottom, from(#129a8e), to(#37ed7d));
  background: linear-gradient(to bottom right, #129a8e, #37ed7d);
}

.badView {
  background: -webkit-gradient(linear, left top, right bottom, from(#e93b28), to(#ff5441));
  background: linear-gradient(to bottom right, #e93b28, #ff5441);
}

.pendingView {
  background: -webkit-gradient(linear, left top, right bottom, from(#fdc830), to(#f37335));
  background: linear-gradient(to bottom right, #fdc830, #f37335);
}

.rejectedView {
  background: -webkit-gradient(linear, left top, right bottom, from(#fdc830), to(#ff2e2e));
  background: linear-gradient(to bottom right, #fdc830, #ff2e2e);
}

.confirmedView {
  background: -webkit-gradient(linear, left top, right bottom, from(#fdc830), to(#1cb179));
  background: linear-gradient(to bottom right, #fdc830, #1cb179);
}

